@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  background-color: #fff;
  color: #1e1e1e;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}

.coin-content {
  -webkit-filter: drop-shadow(0 0 0.75rem #c0c2cc);
          filter: drop-shadow(0 0 0.75rem #c0c2cc);
  padding: 0.3em;
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
}

.coin-input {
  padding-left: 1.5em;
  width: 50em;
  height: 4em;
  border-radius: 0px;
  border-color: #1e1e1e;
  background-color: #dadde2;
  color: #1e1e1e;
}

.coin-input::-webkit-input-placeholder {
  color: #1e1e1e;
}

.coin-input:-ms-input-placeholder {
  color: #1e1e1e;
}

.coin-input::placeholder {
  color: #1e1e1e;
}

.coin-container {
  display: flex;
  justify-content: center;
}

.coin-row {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  height: 4.5em;
  border-bottom: 2px solid #1e1e1e;
  width: 80em;
}

.coin {
  display: flex;
  align-items: center;
  padding-right: 24px;
  min-width: 300px;
}

.coin h1 {
  font-size: 20px;
  width: 8em;
}

.coin img {
  height: 2.9em;
  width: 2.9em;
  margin-right: 0.6em;
}

.coin-symbol {
  text-transform: uppercase;
}

.coin-data {
  display: flex;
  text-align: right;
  justify-content: space-between;
  width: 100%;
}

.coin-price {
  width: 110px;
}

.coin-volume {
  width: 155px;
}

.coin-marketcap {
  width: 230px;
}

.coin-percent {
  width: 100px;
}

.red {
  color: #f00606;
}

.green {
  color: #11d811;
}

h2 {
  color: #1e1e1e;
  margin-top: 2em;
  margin-bottom: 2em;
  text-align: center;
}

